.form {
  background-color: white;
  width: min(100%, 730px);
  padding: 20px;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

.form-section {
  margin-bottom: 40px;
}

.form-section-heading {
  font-size: 24px;
  margin-bottom: 24px;
}

.form-unit {
  margin-bottom: 40px;
}

.form-unit .control {
  display: flex;
  gap: 12px;
}

.form-row {
  margin-bottom: 18px;
}

.form-row label {
  display: block;
  margin-left: 4px;
  margin-bottom: 6px;
}

.form-row label .details {
  color: #aaaaaa;
}

.form-row input,
.form-row textarea {
  display: block;
  width: 100%;
  font-size: 100%;
  padding: 12px;
  border: solid 2px #dcdcdc;
  border-radius: 4px;
}

.form-row textarea {
  resize: none;
  line-height: 150%;
}

.form-row input::placeholder,
.form-row textarea::placeholder {
  color: #aaaaaa;
}

.form-row input[type='file'] {
  display: none;
}

.form-row input[type='file'] + label {
  padding: 12px;
  margin-left: unset;
  margin-right: 14px;
  border: solid 2px #dcdcdc;
  border-radius: 4px;
  display: inline-block;
  cursor: pointer;
}

.form-row input[type='file'] + label + span {
  font-size: 14px;
  color: #818181;
}

.form-control-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.form-control-bar .progress {
  display: flex;
  gap: 16px;
  list-style-type: none;
}

.form-control-bar .progress .bar {
  width: 64px;
  height: 8px;
  border-radius: 4px;
  background-color: #dcdcdc;
}

.form-control-bar .progress .bar.filled {
  background-color: var(--main);
}

.fill-control-bar {
  display: flex;
  gap: 1rem;
}
